.landingPage{
  background-color: black;
  color: white;
}
.emailDiv{
  width: 100%;
  /* z-index: 100; */
  height: 110vh;
  background: rgba(0, 0, 0, 0.4);
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.8) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0) 100%
    );
  color: white;
  }
  .main{
    background: url("https://assets.nflxext.com/ffe/siteui/vlv3/0bd3a69d-6790-4edc-9818-1c8c558946c2/e1a8a32a-a07a-4bae-965a-9b218881763e/IN-en-20220329-popsignuptwoweeks-perspective_alpha_website_large.jpg")
    center no-repeat;
  background-size: cover;
  position: relative;
 
  height: 110vh;
 
}
.nav{
   display: flex; 
   align-items: center;
   justify-content: space-around;
}
.buttons{
  display: flex;
  column-gap: 1em;
  height: 2em;
  margin-right: 3%;
  /* width: 15%; */
  /* margin: 0.6em; */
}
.logo{
 z-index: 200;
  width: 15%;
  margin-left: 2em;
}
.select{
  border: 0.5px solid white;
  padding: 1px;
  background-color: black;
  width: 7em;
  align-items: center;

}
.selectMenu{
 background: transparent;
  border: 0px solid black;
  color: white;
  font-size: 1em;
  margin-top: -20px;
 
  
}
.selectMenu:focus{
  outline: none;

}
.signinButton{
  background-color: rgb(229,9,20);
  padding: 7px 17px;
  color: white;
  border: 1px solid rgb(229,9,20) ;
  border-radius: 3px;
  font-size: 1em;
  width:100px
}
.emailHeading{
  margin-top: 10em;
}
.emailHeading h1{
  font-size: 4.2em;
  text-align: center;
  width: 45%;
  margin: auto;
}
.emailHeading h2{
 font-size: 2em;
 text-align: center;
  width: 45%;
  margin: auto;
  font-weight: 550;
  margin-top: 10px;
}
.emailHeading h3{
  font-size: 1.5em;
 text-align: center;
  width: 50%;
  margin: auto;
  font-weight: 550;
  margin-top: 10px;
}
.mailDiv{
  display: flex;
  width: 50%;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 6em;
}
.mail{
  min-width: 500px;
    height: 70px!important;
    color: white;
    color: #000;
    background-color: white;
    width: fit-content;
    align-self: baseline;
}
.getStarted{
  height: 70px!important;
  padding: 16px 20px;
  font-size: 35px;
  color: #fff;
  border: 0;
  border-radius: 5px;
  background-color: #ef0914;
  cursor: pointer;
  margin-bottom: 20px;
  width: 35%;
}
.horizontal{
  /* height: 8px; */
  border-bottom: 12px solid rgb(45, 44, 44);
  margin-top: -5px;
}
.accordians{
  width: 60%;
  margin: auto;
}
.accordiansChild{
  margin: 10px 0px 10px;
  background-color: #303030;
}
.accordiansSummary{
  height: 4.5em;
  border: 0px;
  background-color: #303030;
  border-bottom: 3px solid black;
}

.headings>h1{
  font-size: 3.125rem;
  line-height: 1.1;
  margin-bottom: 0.5rem;
}
.headings>h2{
  font-size: 1.625rem;
  font-weight: 400;
}
.viewSection{
  display: flex;
  -webkit-box-align: center;
  max-width: 1100px;
  margin: 0 auto;
  padding: 70px 45px;
  align-items: center;
  -webkit-box-pack: justify;
  justify-content: space-between;
}
.faqsHeading{
  font-size: 4em;
  margin: 8% 0% 6% 10%;
}
.faqsHeading2{
  font-size: 1.3em;
  text-align: center;
  margin-top: 5%;
}
.footerDiv{
  width: 60%;
  margin: auto;
  margin-top: 5%;
}
.footerHeading{
  font-size: 20px;
  font-weight: 550;
  color:#757575

}
.footerLink{
  margin-top: 2em;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  row-gap: 2em;
}
.link {
  color: #757575;
  text-decoration: none;
  font-size: 15px;
  font-weight: lighter;
}
.link:hover{
  text-decoration: underline;
}
.footerNetflix{
  color: #757575;
  font-size: 18px;
  font-weight: lighter;
  padding-bottom: 3em;
}
.footerSelect{
  border: 0.5px solid #757575;
  padding: 10px;
  background-color: black;
  width: 8em;
  align-items: center;
  margin-top: 3%;
  color:#757575

}
.footerSelectMenu{
 background: transparent;
  border: 0px solid black;
  color: #757575;
  font-size: 1em;
  margin-top: -20px;
 
  
}
.footerSelectMenu:focus{
  outline: none;
}
