.logo img {
  height: 50px;
  position: absolute;
  left: 0;
  margin-left: 50px;
  margin-top: 20px;
}
.header {
  display: flex;
  justify-content: space-between;
}

.header a {

  right: 50px;
  top: 30px;
  text-decoration: none;
  color: gray;
  font-size: 1.2em;
  font-weight: 600;
}

.middlebox {
  width: 250px;
  margin-top: 10%;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
}

.middlebox img {
  width: 100%;
}

.middlebox button {
  width: 100%;
  height: 60px;
  margin: 5px;
  border-radius: 5px;
  padding: 10px;
  background-color: red;
  border: 0;
  color: white;
  font-size: 1.3em;
  margin-top: 20px;
  cursor: pointer;
}

